



















































































import { Component, Vue, Prop } from 'vue-property-decorator'
import Page from '@/components/common-components/page.vue'
import { EventBus } from '../../until/event-bus'
@Component({
  components: {
    Page
  },
  filters: {
    stringToNumber (value: string) {
      return parseInt(value)
    }
  }
})
export default class carInfo extends Vue {
  $createDialog: any
  $createToast: any
  $refs: any
  carId: any
  dateTimePicker: any
  $createDatePicker: any
  departureTime = ''
  $route: any
  currentLineList = []
  selectedIndex = []
  model: any = {
    num: '',
    contacts_name: '',
    contacts_phone: '',
    licensePlate: '',
    departurePlace: '',
    departureTime: '',
    complete: false,
    controlLine: false,
    startLimit: [],
    endLimit: []
  }
  fields = [
    { type: 'input', modelKey: 'num', label: '票数', props: { placeholder: '请输入', type: 'number' }, rules: { required: true } },
    { type: 'input', modelKey: 'contacts_name', label: '负责人', props: { placeholder: '请输入' }, rules: { required: true } },
    { type: 'input', modelKey: 'contacts_phone', label: '联系电话', props: { placeholder: '请输入', type: 'number' }, rules: { required: true } },
    { type: 'input', modelKey: 'licensePlate', label: '车牌', props: { placeholder: '请输入' }, rules: { required: false } },
    { type: 'input', modelKey: 'departurePlace', label: '发车地点', props: { placeholder: '请输入' }, rules: { required: true } },
    { type: 'input', modelKey: 'departureTime', label: '发车时间', props: { placeholder: '请输入' }, rules: { required: true } },
    { type: 'switch', modelKey: 'complete', label: '业务完成', props: { placeholder: '请输入' } },
    { type: 'switch', modelKey: 'controlLine', label: '配置区间票', props: { placeholder: '请输入' } }
  ]
  created () {
    this.carId = this.$route.query.carId
    Object.keys(this.$route.query.carInfo).forEach((key) => {
      this.model[key] = this.$route.query.carInfo[key]
    })
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get fleetId () {
    return this.$route.query.fleetId
  }
  get showComplete () {
    let date = this.model.departureTime
    return new Date() > new Date(date)
  }
  submitHandler (e: any) {
    e.preventDefault()
    this.$store.dispatch('addOrUpdateCar', this.model).then((response) => {
      if (response.success) {
        const toast = this['$createToast']({
          time: 3000,
          txt: '更新成功',
          type: 'txt'
        })
        toast.show()
        Object.keys(this.model).forEach((key) => {
          this.$route.query.carInfo[key] = this.model[key]
        })
        EventBus.$emit('carInfoChange', this.$route.query.carInfo)
        this.$router.back()
      } else {
        const toast = this['$createToast']({
          time: 1000,
          txt: '更新失败',
          type: 'txt'
        })
        toast.show()
      }
    })
  }
  submitClick () {
    return false
  }
  showDatePicker () {
    let date = this.model.departureTime
    let dateValue = new Date(date)
    this.dateTimePicker = this.$createDatePicker({
      title: '发车时间选择',
      min: new Date(2008, 7, 8, 8, 0, 0),
      max: new Date(2030, 9, 20, 20, 59, 59),
      value: dateValue,
      columnCount: 6,
      onSelect: this.dateSelectHandle,
      onCancel: this.dateCancelHandle
    })
    this.dateTimePicker.show()
  }
  dateSelectHandle (date: any) {
    console.log(date)
    let dateResult = this.getNowFormatDate(date)
    this.model['departureTime'] = dateResult
  }
  dateCancelHandle () { }
  getNowFormatDate (date: any) {
    let seperator1 = '-'
    let seperator2 = ':'
    let month = date.getMonth() + 1
    let strDate = date.getDate()
    let hours = date.getHours()
    let minutes = date.getMinutes()
    let seconds = date.getSeconds()
    if (month >= 1 && month <= 9) {
      month = '0' + month
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = '0' + strDate
    }
    if (hours >= 0 && hours <= 9) {
      hours = '0' + hours
    }
    if (minutes >= 0 && minutes <= 9) {
      minutes = '0' + minutes
    }
    if (seconds >= 0 && seconds <= 9) {
      seconds = '0' + seconds
    }
    let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + hours + seperator2 + minutes + seperator2 + seconds
    return currentdate
  }
  limitHandle (type: string) {
    let promet = {
      value: '',
      placeholder: '请输入'
    }
    this.$createDialog({
      type: 'prompt',
      title: '添加区间地',
      prompt: promet,
      onConfirm: (e: any, promptValue: string) => {
        if (this.model[type].length > 0) {
          let fleg = false
          this.model[type].some((item: any) => {
            if (item.name === promptValue) {
              fleg = true
              return true
            }
          })
          if (fleg) {
            return
          }
        }
        this.model[type].push({
          name: promptValue,
          num: 0
        })
      }
    }).show()
  }

  deleteLimit (type: string, index: number) {
    this.model[type].splice(index, 1)
  }
}
